<template>
  <div>
    <!--
    <van-nav-bar
      v-if="isLogin"
      :title="title"
      left-text="返回"
      right-text="运营管理"
      left-arrow
      fixed
      @click-left="onClickLeft"
      @click-right="onClickRight"
    />
    -->
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <div class="mall_list">
      <div class="mall_item" v-for="(item,index) in list" @click="toDetailPage(index)">
        <img :src="item.imageURL" alt="">
        <div class="mall_item_name">{{item.name}}</div>
      </div>
    </div>
    <van-popup v-model="isDisclaimer">
      <div class="disclaimer">
        <h5 class="title">免责声明</h5>
        <div class="textContent">
          <p>本报价板（结构金融-利率互换报价板和结构金融-信用违约互换报价版）为国泰君安证券股份有限公司（以下简称“国泰君安证券”）依法设立、运营的官方报价板。本报价板是机密的，只有经国泰君安证券认可的订阅者才能使用。</p>
          <p>本报价板推送的信息仅限发布当日有效。本报价板所载的信息、材料或分析工具只提供给订阅者作参考之用，不是或不应被视为出售、购买或认购证券或其他金融工具的要约或要约邀请。在任何情况下，本报价板所载内容不构成任何投资建议，国泰君安证券也不对任何因使用本报价板所载任何内容所引致或可能引致的损失承担任何责任。订阅者不应以此取代自己的独立判断，应自主做出投资决策并自行承担所有投资风险。国泰君安证券也不因订阅者可订阅本报价板而视其为国泰君安证券的当然客户。</p>
          <p>国泰君安证券对本报价板所载信息保留一切法律权利。订阅者如需对本报价板所载内容复制、转载给任一第三方的，应事先得到国泰君安证券的许可，并需注明出处，且不得对本报价板所载内容进行任何有悖原意的引用、删节和修改，如因侵权行为给国泰君安证券造成任何直接或间接损失的，国泰君安证券保留追究一切法律责任的权利。</p>
        </div>
        <div class="foot">
          <div class="check">
            <van-checkbox v-model="checked" checked-color="#DDA944"></van-checkbox>
            <span @click="chooseText">勾选后表示您已经知悉国泰君安免责声明</span>
          </div>
          <div class="makeSure" v-show="!timeShow">{{count}}s</div>
          <div class="makeSure" v-show="timeShow">
            <span :class="checked === false ? '' : 'choosed'" @click="goTo">确定</span>
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
  export default {
    name: "index",
    data(){
      return {
        isLogin: false,
        isDisclaimer: false, // 免责声明
        checked: false,
        title: '旗舰店',
        timeShow: true,
        count: '',
        timer: null,
        list: [
          {imageURL: require('../../assets/images/mall/zcx.png'), name: '中诚信国际信用评级有限公司', path: '/mall/zcx'},
          {imageURL: require('../../assets/images/mall/zzzx.png'), name: '中债资信评估有限责任公司', path: '/mall/zzzx'},
          {imageURL: require('../../assets/images/mall/gtja.png'), name: '国泰君安证劵', path: '/mall/gtja'},
        ]
      }
    },
    methods:{
      onClickLeft(){

        // if(this.isAndroid()){
        //   alert('安卓');
        // }else if (this.isiOS()){
        //   alert('ios')
        // }
        this.isAndroid();
        this.isiOS();
        // WebViewJavascriptBridge.callHandler('gobackapp');
      },
      // 跳转运营管理
      onClickRight () {
        this.$router.push('/mall/yygl');
      },
      toDetailPage(index){
        if(index === 2){
          if(this.isLogin){
            // 弹出免责声明
            this.isDisclaimer = true;
            this.getCode();
          }else{
            this.$dialog.confirm({
              title: '友情提示',
              message: '您还未登录或登录已超时，请先登录？'
            }).then(() => {
              //跳转app登录页面
             nativeInterface.toapplogin();
            }).catch(() => {

            });
          }
        }else{
          this.$router.push(this.list[index].path);
        }
      },
      // 点击文字勾选
      chooseText () {
        this.checked = !this.checked
      },
      // 倒计时
      getCode(){
        const TIME_COUNT = 10;
        if (!this.timer) {
          this.count = TIME_COUNT;
          this.timeShow = false;
          this.timer = setInterval(() => {
            if (this.count > 1 && this.count <= TIME_COUNT) {
              this.count--;
            } else {
              this.timeShow = true;
              clearInterval(this.timer);
              this.timer = null;
            }
          }, 1);
        }
      },
      goTo () {
        if (this.checked === true) {
          this.$router.push(this.list[2].path);
        } else {
          this.$toast('请勾选免责声明');
        }
      },
      //ios WKWebView方法调用初始化
      setupWebViewJavascriptBridge(callback) {
        if (window.WebViewJavascriptBridge) { return callback(WebViewJavascriptBridge); }
        if (window.WVJBCallbacks) { return window.WVJBCallbacks.push(callback); }
        window.WVJBCallbacks = [callback];
        var WVJBIframe = document.createElement('iframe');
        WVJBIframe.style.display = 'none';
        WVJBIframe.src = 'wvjbscheme://__BRIDGE_LOADED__';
        document.documentElement.appendChild(WVJBIframe);
        setTimeout(function() { document.documentElement.removeChild(WVJBIframe) }, 0)
      },
      //判断android ios
      isAndroid(){
        var u = navigator.userAgent, app = navigator.appVersion;
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //android终端或者uc浏览器  
        alert(isAndroid);
        // return isAndroid;
      },
      isiOS() {
        var u = navigator.userAgent, app = navigator.appVersion;
        var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端  
        alert(isiOS);
        // return isiOS
      }
    },
    created() {
      let token = "eyJ0eXAiOiJKV1QiLCJhbGciOiJSUzI1NiJ9.eyJ1c2VyT2JqZWN0Ijp7InVzZXJJZCI6IjQ2MDE2IiwidXNlck5hbWUiOiLpn6nlv5flvawiLCJ1c2VyTWFpbCI6bnVsbCwidXNlclJlYWxOYW1lIjoi6Z-p5b-X5b2sIiwidXNlck9yZ0lkIjoiMTMzMjMiLCJ1c2VyT3JnTmFtZSI6IuWAuuadg-a1i-ivleaJv-.S6owVxv-KsX1-WJcdBrrqz41Xh-xqLM8QVhcFymaAP-Q9OzBi36vJb9G_VBrwftuORpHbJLokNmeM2CQoc0O1ptrX3_p7irqcPT8hcCKd1T0Y0Ef9Ig3EXv6QepglpaNYwrWgjzDv7ylAeE6WxEQYS9ekS5Uq3lLhZQYt4RVXKY";
      // let token = this.$route.query.token;

      if(token){
        this.isLogin = true;
        sessionStorage.setItem('token', token);
      }
    },
    mounted() {
      this.setupWebViewJavascriptBridge(function(bridge) {
        bridge.registerHandler('Get', function(data, responseCallback) {
          alert(data);
        })
      })
    }
  }
</script>

<style scoped>

  .mall_list {
    margin-top: 46px;
    padding: 15px;
  }
  .mall_item {
    width: 100%;
    height: 75px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 4px 6px 0px rgba(0, 0, 0, 0.19);
    border-radius: 2px;
    margin-bottom: 10px;
    position: relative;
  }
  .mall_item img {
    width: 60px;
    height: 60px;
    position: absolute;
    left: 11px;
    top: 0;
    bottom: 0;
    margin: auto;
  }
  .mall_item_name {
    position: absolute;
    top: 25.5px;
    left: 85.5px;
    right: 11px;
    font-size: 16px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(32, 32, 32, 1);
    line-height: 22.5px;
    letter-spacing: 0.5px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .van-popup {
    border-radius: 13px;
  }
  .disclaimer {
    width: 345px;
    height: 485px;
    display: flex;
    flex-direction: column;
  }
  .title {
    text-align: center;
    font-size:18px;
    font-family:PingFangSC-Semibold;
    font-weight:600;
    color:rgba(74,74,74,1);
    line-height:25px;
    padding: 20px 0 6px;
  }
  .textContent {
    flex: 1;
    padding: 0 20px;
    overflow: auto;
  }
  .textContent p {
    text-indent: 2em;
    font-size:13px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(155,155,155,1);
    line-height:24px;
  }
  .foot {
    padding: 10px 0;
    display: flex;
    flex-direction: column;
  }
  .check {
    display: flex;
    align-items: center;
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
  }
  .check span {
    font-size:14px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(124,124,125,1);
    line-height:20px
  }
  .makeSure {
    text-align: center;
    font-size:18px;
    padding-top: 11px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(221,169,68,1);
    line-height:25px;
  }
  .makeSured {
    text-align: center;
    font-size:18px;
    padding-top: 11px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(221,169,68,1);
    line-height:25px;
  }
  .choosed {
    font-size:17px;
    font-family:PingFangSC-Medium;
    font-weight:500;
    color:rgba(255,255,255,1);
    line-height:24px;
    padding: 6px 18px;
    background:rgba(221,169,68,1);
    box-shadow:0px 8px 10px 0px rgba(221,169,68,0.5);
    border-radius:28px
  }
  .van-checkbox {
    margin: 0 9px 0 15px;
  }
</style>



